import React, { Component } from 'react';
import styled from 'styled-components';
import Theme from '../../theme';
import { remCalc } from '@lux/helpers';

import { Grid, Row, Column, Heading, OptusButton } from '../../index';

import Tile1 from './tile-1.png';
import Tile2 from './tile-2.png';
import Tile3 from './tile-3.png';
import Tile4 from './tile-4.png';
import Tile5 from './tile-5.png';

const TilesContainerWrapper = styled.div`
  padding-bottom: 100px;
`;

const TilesMobileMargin = styled.div`
  @media only screen and (max-width: 852px) {
    margin-bottom: 100px;
  }
`;

const TileHeading = styled.h1`
  font-size: 48px;
  text-align: center;
  line-height: 1.2;
  margin-top: 72px;
  margin-bottom: 48px;
`;

const TileImage = styled.img`
  width: 100%;
`;

const TileTextContainer = styled.div`
  height: 200px;
  @media only screen and (max-width: 852px) {
    height: auto;
  }
`;

const TileSectionHeading = styled.p`
  font-size: 32px;
  font-weight: 700;
  line-height: 1.2;
  margin-top: 24px;
  margin-bottom: 12px;
`;

const TileDescription = styled.p`
  font-size: 16px;
  line-height: 1.5;
  margin-top: 0px;
  margin-bottom: 24px;
`;

const FeatureTileWrapper = styled.div`
  font-size: 16px;
  line-height: 1.5;
  position: relative;
  padding-bottom: 300px;
`;

const FeatureTileContent = styled.div`
  padding: 20px;
  position: absolute;
  background: rgb(255, 255, 255);
  top: 180px;
  width: 360px;
  left: 50%;
  transform: translateX(-50%);

  h1 {
    margin: 0;
  }
`;

const FeatureSectionHeading = styled.p`
  font-size: 32px;
  font-weight: 700;
  line-height: 1.2;
  margin-top: 0;
  margin-bottom: 12px;
`;

const FeatureDescription = styled.p`
  font-size: 16px;
  line-height: 1.5;
  margin-top: 0px;
  margin-bottom: 24px;
`;

const FeatureTextContainer = styled.div`
  height: 220px;
  @media only screen and (max-width: 852px) {
    height: auto;
  }
  margin: 0;
`;

const FeatureHeading = styled.h1`
  font-size: 48px;
  text-align: center;
  line-height: 1.2;
  margin-top: 0;
  margin-bottom: 48px;
`;

class TilesContainer extends Component {
  render() {
    if (this.props.type === 'generic') {
      return (
        <TilesContainerWrapper>
          <Grid>
            <Row>
              <Column sm={12} md={12} lg={12}>
                <TileHeading>{this.props.sectionHeading}</TileHeading>
              </Column>
              {this.props.tiles.map((value, index) => {
                var tilePath = null;
                if (value.image === './tile-1.png') {
                  tilePath = Tile1;
                } else if (value.image === './tile-2.png') {
                  tilePath = Tile2;
                } else if (value.image === './tile-3.png') {
                  tilePath = Tile3;
                }
                return (
                  <Column key={index} sm={12} md={12} lg={4}>
                    <TilesMobileMargin>
                      <TileImage src={tilePath} />
                      <TileTextContainer>
                        <TileSectionHeading>{value.heading}</TileSectionHeading>
                        <TileDescription>{value.description}</TileDescription>
                      </TileTextContainer>
                      <OptusButton>{value.ctaText}</OptusButton>
                    </TilesMobileMargin>
                  </Column>
                );
              })}
            </Row>
          </Grid>
        </TilesContainerWrapper>
      );
    }

    if (this.props.type === 'feature') {
      return (
        <TilesContainerWrapper>
          <Grid>
            <Row>
              <Column sm={12} md={12} lg={12}>
                <FeatureHeading>{this.props.sectionHeading}</FeatureHeading>
              </Column>
              {this.props.tiles.map((value, index) => {
                var tilePath = null;
                if (value.image === './tile-4.png') {
                  tilePath = Tile4;
                } else if (value.image === './tile-5.png') {
                  tilePath = Tile5;
                }
                return (
                  <Column key={index} sm={12} md={12} lg={6}>
                    <TilesMobileMargin>
                      <FeatureTileWrapper>
                        <TileImage src={tilePath} />
                        <FeatureTileContent>
                          <FeatureTextContainer>
                            <FeatureSectionHeading>
                              {value.heading}
                            </FeatureSectionHeading>
                            <FeatureDescription>
                              {value.description}
                            </FeatureDescription>
                          </FeatureTextContainer>
                          <OptusButton>{value.ctaText}</OptusButton>
                        </FeatureTileContent>
                      </FeatureTileWrapper>
                    </TilesMobileMargin>
                  </Column>
                );
              })}
            </Row>
          </Grid>
        </TilesContainerWrapper>
      );
    }
  }
}

export default TilesContainer;
